import React from "react";
import PropTypes from 'prop-types';
import style from "./OrderDetails.module.css";
import imgDone from "../../../image/done.svg";

export default function OrderDetails (props) {

  return (
    <div className={style.OrderDetails}>
      <p className={style.OrderDetailsNumber + " text text_type_digits-large pt-4"}> { props.number } </p>
      <p className="text text_type_main-small pt-8"> </p>
      <img className="pt-15 pb-15" src={imgDone} alt="" />
      <p className="text text_type_main-default">   </p>
      <p className=" =text text_type_main-default text_color_inactive pt-2 pb-15">    </p>
    </div>
  );
};

OrderDetails.propTypes = {
  number: PropTypes.oneOfType([
    PropTypes.string.isRequired,
    PropTypes.number.isRequired])
}; 